.navbar{
    display: flex;
    height: 80px;
    background-color: white;
    width: 100%;
    justify-content: space-between;
    position: fixed;
    top:0;
    z-index: 1;
}

.navbar-brand{
    flex: 2;
    margin: auto;
}

.navbar-brand img{
    transform: scale(0.4);
}

.navbar-items{
    justify-content: end;
    flex: 5;
    display: flex;
    list-style-type: none;
}

.navbar-item{
    margin: auto;
    text-decoration: none;
    padding: auto 50px;
    text-align: unset;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.navbar-item a{
    line-height: 80px;
    text-decoration: none;
    color: rgb(53, 74, 199);
}

.navbar-item:hover a{
    cursor: pointer;
    color: orange;
}

.navbar-items-mobile{
    display: none;
}
.navbar-items-mobile i{
    cursor: pointer;
}

/* -----------------------------sub-nav */
.navbar-item:hover>.sub-nav{
    display: block;
}

.sub-nav{
    background-color: rgb(76, 173, 238);
    display: none;
    position: absolute;
    list-style-type: none;
    top:80px
}
.sub-nav li{
    text-decoration: underline;
    padding: 20px 20px;
    transition: all 2s;
}
.sub-nav li:hover{
    cursor: pointer;
    color: orange;
    background-color: rgba(0, 255, 255, 0.479);
}


@media (max-width:800px) {
    .navbar-items-mobile{
        flex: 3;
        display: flex;
        justify-content:end;
        align-items:center;
    }
    .navbar-items-mobile i{
        margin-right: 50px;
        font-size: 25px;
    }
    .navbar-items{
        display:flex;
        flex-direction: column;
        position: absolute;
        left:-50%;
        top:80px;
        transition: all 0.5s;
    }
    .navbar-items.active{
        width: 100%;
        display:flex;
        flex-direction: column;
        position: absolute;
        left:0%;
        top:80px;
        transition: all 0.5s;
        background-color: aliceblue;
    }

    .navbar-item{
        display: flex;
        margin: 0;
        justify-content: center;
    }
    
    .navbar-item:hover{
        background-color: rgb(204, 205, 248);
    }
    .sub-nav{
        top:0px;
        position: relative;
        
        width: 100%;
        background-color: rgb(214, 169, 169);
    }
    .sub-nav li{
        display: flex;
        justify-content: center;
        margin: auto;
        border-top: 4px white solid;
    }
}