.slides-container{
    margin-top: 100px;
    height: 120vh;
    display: flex;
    flex-direction: column;
}
.slides-container>h1{
    padding: auto;
    text-align: center;
    font-size: 3rem;
    margin-bottom: 100px;
}

.slide{ 
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.img-container1{
    transition: all 1s ease-in;
    width: 70%;
    position: relative;
}

.slide>button{
    height: 100%;
    border: none;
    width: 2rem;
    font-size: 2rem;
}

.info-hotel{
    position: absolute;
    bottom: 0;
    left: 0px;
    right: 70%;
    margin-bottom: 3rem;
    margin-left: 3rem;
    color: aliceblue;
}

.wrap-info-hotel{
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
}

.wrap-info-hotel:hover{
    background-color: rgba(0, 0, 0, 0.315);
}

.info-hotel button{
    margin-top: 1rem;
    width: 7rem;
    height: 3rem;
    cursor: pointer;
}

@media(max-width:700px){
    .info-hotel p{
        display: none;
    }

}
